*{margin:0;padding:0;box-sizing:border-box}body{width:100vw;height:100vh;font-family:"Roboto",sans-serif;color:#fff}.search_box{position:relative;width:40%}.search_box input[type='text']{width:100%;padding:10px 10px 10px 15px;box-sizing:border-box;border:2px solid #fff;border-radius:25px;outline:none;box-shadow:0 0 4px rgba(0,0,0,0.25)}.search{position:absolute;top:50%;width:30px;height:20px;right:6px;z-index:4;font-size:25px;fill:#172a3a;border-left:1px solid #172a3a;transform:translateY(-50%);cursor:pointer}.results{position:relative;z-index:4;width:100%;height:400px;border-radius:22px;overflow:hidden;overflow-y:scroll}.results::-webkit-scrollbar{width:10px;border-radius:7px;background:white}.results::-webkit-scrollbar-thumb{background:#74b3ce;border-radius:7px}.background{position:absolute;top:100%;z-index:3;width:100%;height:400px;border-radius:22px;background-color:white;backdrop-filter:blur(250px);opacity:0.65}.error{text-align:center;color:#172a3a}.hidden{display:none}@media screen and (max-width: 768px){.search_box{width:90%;justify-self:center}}
