*{margin:0;padding:0;box-sizing:border-box}body{width:100vw;height:100vh;font-family:"Roboto",sans-serif;color:#fff}.card{position:relative;z-index:1;top:3%;left:3%;display:flex;width:92%;height:29%;margin-bottom:3%;border-radius:22px;background-color:#fff;color:#172a3a;overflow:hidden}.card__image{width:54.5%;height:auto}.card__image--apartment{width:100%;height:100%;object-fit:cover}.card__image--icon{position:absolute;top:7%;left:2%;cursor:pointer}.card__description{display:flex;flex-direction:column;justify-content:space-between;width:45.5%}.profile{padding:4% 0 0 4%}.profile h1{margin-bottom:5px;font-size:20px}.profile__detail{display:flex;align-items:center}.profile__detail--photo{width:22px;height:22px;margin-right:5px;border-radius:50%;overflow:hidden}.profile__detail--photo img{width:100%;height:100%;object-fit:cover}.priceAndButtom{display:flex;justify-content:space-between;padding:0 4% 4% 4%}.priceAndButtom__buttom{width:79px;height:23px;border-radius:25px;font-size:12px;background-color:#172a3a;color:#fff;outline:none;border:none;cursor:pointer}@media screen and (max-width: 768px){.card{width:95%;height:10%;left:1.8%;margin-bottom:5%;box-shadow:5px 0 10px rgba(0,0,0,0.25)}.profile h1{font-size:34px}.profile__detail--name{font-size:16px}.priceAndButtom{font-size:20px}}@media screen and (max-width: 425px){.profile h1{font-size:18px}.profile__detail--name{font-size:14px}.card__image{width:47%}.card__description{width:53%}.priceAndButtom{font-size:14px}}
